 * {
     margin: 0;
     padding: 0;
     outline: 0;
     box-sizing: border-box;
 }

html, body, #root{
    height: 100%;
}

body {
    background: #f5f5f5;
}

body, input, button{
    font-family: Arial, Helvetica, sans-serif;
}



nav{
    width: 100%;
    height: 60px;
    background: rgba(77, 1, 112, 0.7);
    border-bottom: 1px solid rgba(174, 0, 255, 0.7);
}

label{
    margin: 0 40px 0 0;
    font-size: 40px;
    line-height: 55px;    
    display: none;
    width: 30px;
    float: right;
}

#toggle{
    display: none;
}

nav ul{    
    display: flex;
    padding: 0;
    float: right;
    position: sticky;    
    list-style: none;   
}

nav ul li{
    text-decoration: none;
}

nav ul li a{
    display: block;
    color: black;
    padding: 12px 40px;
    text-decoration: none;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 20px;
    position: relative;
}


nav ul li a:hover{
    color: rgba(174, 0, 255, 0.7);
}

nav a img{
    width: 130px;
    height: 55px;
    color: #fff;
    float: left;
    margin-left: 3%
}

@media only screen and (max-width: 1117px){    
    label {
        display: block;
        cursor: pointer;
    }
    ul{
        text-align: center;
        width: 100%;  
    }
    ul li a{
        border-bottom: 0.8px solid rgba(174, 0, 255, 0.7);
        width: 95%;
        margin-left: 2.5%;
    }
    #toggle:not(:checked) ~ ul li{
        display: none; 
    }
    #toggle:checked ~ ul{
        display: block;        
    } 
    

}

.background-image{
    background-position: center center;
    background-image: url('../assets/background.jpg');
    background-size: cover;
	background-attachment: fixed;
    background-repeat: no-repeat;    
    min-height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    height: auto;
    width: 100%;
    filter: blur(15px);
    opacity: 0.65;
    z-index: -1;
    
}

